<template>
  <div class="role-management-container">
    <div v-if="!isUserAdmin" class="access-denied">
      <el-result
        icon="warning"
        title="Akses Ditolak"
        subTitle="Anda tidak memiliki akses untuk mengelola role sistem."
      >
      </el-result>
    </div>
    
    <div v-else>
      <div class="header-section">
        <h1 class="page-title">Manajemen Role & Authorities</h1>
        <p class="page-description">Kelola role sistem dan hak akses pengguna</p>
      </div>

      <el-card class="content-card">
        <div slot="header" class="card-header">
          <span class="card-title">Daftar Role Sistem</span>
          <el-button type="primary" icon="el-icon-plus" @click="showCreateDialog">
            Tambah Role Baru
          </el-button>
        </div>

        <!-- Role Table -->
        <el-table
          :data="paginatedRoles"
          style="width: 100%"
          v-loading="loading"
          element-loading-text="Memuat data role..."
        >
          <el-table-column prop="roleCode" label="Kode Role" width="150">
          </el-table-column>
          <el-table-column prop="roleName" label="Nama Role" width="200">
          </el-table-column>
          <el-table-column prop="description" label="Deskripsi">
          </el-table-column>
          <el-table-column prop="enabled" label="Status" width="120" align="center">
            <template slot-scope="scope">
              <el-tag :type="scope.row.enabled ? 'success' : 'danger'">
                {{ scope.row.enabled ? 'Aktif' : 'Tidak Aktif' }}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column label="Aksi" width="300" align="center">
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="primary"
                icon="el-icon-edit"
                @click="showEditDialog(scope.row)"
              >
                Edit
              </el-button>
              <el-button
                size="mini"
                :type="scope.row.enabled ? 'warning' : 'success'"
                :icon="scope.row.enabled ? 'el-icon-close' : 'el-icon-check'"
                @click="toggleRoleStatus(scope.row)"
              >
                {{ scope.row.enabled ? 'Non-aktifkan' : 'Aktifkan' }}
              </el-button>
              <el-button
                size="mini"
                type="danger"
                icon="el-icon-delete"
                @click="confirmDeleteRole(scope.row)"
              >
                Hapus
              </el-button>
            </template>
          </el-table-column>
        </el-table>

        <!-- Pagination -->
        <div class="pagination-container">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[10, 20, 50, 100]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="filteredRoles.length"
          >
          </el-pagination>
        </div>
      </el-card>

      <!-- User Role Assignment Card -->
      <el-card class="content-card" style="margin-top: 20px;">
        <div slot="header" class="card-header">
          <span class="card-title">Assign Role ke User</span>
        </div>

        <el-row :gutter="20">
          <el-col :span="8">
            <el-form-item label="User ID">
              <el-input 
                v-model="userRoleForm.userId" 
                placeholder="Masukkan User ID"
                clearable
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="Role">
              <el-select v-model="userRoleForm.roleName" placeholder="Pilih Role" clearable>
                <el-option
                  v-for="role in roles"
                  :key="role.roleCode"
                  :label="role.roleName"
                  :value="role.roleName"
                >
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label=" ">
              <el-button type="primary" @click="assignRole">Assign Role</el-button>
              <el-button type="danger" @click="removeRole">Remove Role</el-button>
              <el-button type="info" @click="getUserRoles">Cek Role User</el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-card>

      <!-- Create/Edit Role Dialog -->
      <el-dialog 
        :title="isEditing ? 'Edit Role' : 'Tambah Role Baru'"
        :visible.sync="showRoleDialog"
        width="500px"
      >
        <el-form :model="roleForm" :rules="roleRules" ref="roleForm">
          <el-form-item label="Kode Role" prop="roleCode">
            <el-input 
              v-model="roleForm.roleCode" 
              placeholder="Masukkan kode role (akan otomatis uppercase)"
              :disabled="isEditing"
            ></el-input>
          </el-form-item>
          <el-form-item label="Nama Role" prop="roleName">
            <el-input 
              v-model="roleForm.roleName" 
              placeholder="Masukkan nama role (akan otomatis uppercase)"
            ></el-input>
          </el-form-item>
          <el-form-item label="Deskripsi" prop="description">
            <el-input 
              v-model="roleForm.description" 
              type="textarea" 
              :rows="3"
              placeholder="Masukkan deskripsi role"
            ></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="showRoleDialog = false">Batal</el-button>
          <el-button type="primary" @click="saveRole" :loading="saving">
            {{ isEditing ? 'Update' : 'Simpan' }}
          </el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'RoleManagementHome',
  data() {
    return {
      loading: false,
      saving: false,
      currentPage: 1,
      pageSize: 10,
      showRoleDialog: false,
      isEditing: false,
      roleForm: {
        roleCode: '',
        roleName: '',
        description: ''
      },
      userRoleForm: {
        userId: '',
        roleName: ''
      },
      roleRules: {
        roleCode: [
          { required: true, message: 'Kode role wajib diisi', trigger: 'blur' },
          { min: 3, max: 20, message: 'Kode role harus 3-20 karakter', trigger: 'blur' }
        ],
        roleName: [
          { required: true, message: 'Nama role wajib diisi', trigger: 'blur' },
          { min: 3, max: 50, message: 'Nama role harus 3-50 karakter', trigger: 'blur' }
        ],
        description: [
          { required: true, message: 'Deskripsi wajib diisi', trigger: 'blur' },
          { max: 200, message: 'Deskripsi maksimal 200 karakter', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    ...mapGetters('auth', ['user']),
    ...mapGetters('roles', ['roles']),
    isUserAdmin() {
      return this.user && this.user.authorities && 
             this.user.authorities.some(auth => auth.authority === 'ROLE_ADMIN')
    },
    filteredRoles() {
      return this.roles || []
    },
    paginatedRoles() {
      const start = (this.currentPage - 1) * this.pageSize
      const end = start + this.pageSize
      return this.filteredRoles.slice(start, end)
    }
  },
  async created() {
    if (this.isUserAdmin) {
      await this.loadRoles()
    }
  },
  methods: {
    ...mapActions('roles', [
      'fetchRoles', 
      'createRole', 
      'updateRole', 
      'deleteRole',
      'toggleRoleEnabled',
      'assignUserRole',
      'removeUserRole',
      'fetchUserRoles'
    ]),
    
    async loadRoles() {
      this.loading = true
      try {
        await this.fetchRoles()
      } catch (error) {
        this.$message.error('Gagal memuat data role: ' + error.message)
      } finally {
        this.loading = false
      }
    },

    showCreateDialog() {
      this.isEditing = false
      this.roleForm = {
        roleCode: '',
        roleName: '',
        description: ''
      }
      this.showRoleDialog = true
      this.$nextTick(() => {
        this.$refs.roleForm && this.$refs.roleForm.clearValidate()
      })
    },

    showEditDialog(role) {
      this.isEditing = true
      this.roleForm = {
        roleCode: role.roleCode,
        roleName: role.roleName,
        description: role.description
      }
      this.showRoleDialog = true
      this.$nextTick(() => {
        this.$refs.roleForm && this.$refs.roleForm.clearValidate()
      })
    },

    async saveRole() {
      try {
        const isValid = await this.$refs.roleForm.validate()
        if (!isValid) return

        this.saving = true
        
        if (this.isEditing) {
          await this.updateRole({
            roleName: this.roleForm.roleCode, // Use original code as identifier
            roleData: this.roleForm
          })
          this.$message.success('Role berhasil diupdate')
        } else {
          await this.createRole(this.roleForm)
          this.$message.success('Role berhasil dibuat')
        }
        
        this.showRoleDialog = false
        await this.loadRoles()
      } catch (error) {
        this.$message.error('Gagal menyimpan role: ' + error.message)
      } finally {
        this.saving = false
      }
    },

    async toggleRoleStatus(role) {
      try {
        await this.toggleRoleEnabled({
          roleName: role.roleName,
          enabled: !role.enabled
        })
        this.$message.success(`Role berhasil ${role.enabled ? 'dinonaktifkan' : 'diaktifkan'}`)
        await this.loadRoles()
      } catch (error) {
        this.$message.error('Gagal mengubah status role: ' + error.message)
      }
    },

    confirmDeleteRole(role) {
      this.$confirm(
        `Apakah Anda yakin ingin menghapus role "${role.roleName}"? Tindakan ini tidak dapat dibatalkan.`,
        'Konfirmasi Hapus',
        {
          confirmButtonText: 'Ya, Hapus',
          cancelButtonText: 'Batal',
          type: 'warning'
        }
      ).then(async () => {
        try {
          await this.deleteRole(role.roleName)
          this.$message.success('Role berhasil dihapus')
          await this.loadRoles()
        } catch (error) {
          this.$message.error('Gagal menghapus role: ' + error.message)
        }
      }).catch(() => {
        // User cancelled
      })
    },

    async assignRole() {
      if (!this.userRoleForm.userId || !this.userRoleForm.roleName) {
        this.$message.warning('User ID dan Role harus diisi')
        return
      }
      
      try {
        await this.assignUserRole({
          userId: this.userRoleForm.userId,
          roleName: this.userRoleForm.roleName
        })
        this.$message.success('Role berhasil di-assign ke user')
        this.userRoleForm = { userId: '', roleName: '' }
      } catch (error) {
        this.$message.error('Gagal assign role: ' + error.message)
      }
    },

    async removeRole() {
      if (!this.userRoleForm.userId || !this.userRoleForm.roleName) {
        this.$message.warning('User ID dan Role harus diisi')
        return
      }
      
      try {
        await this.removeUserRole({
          userId: this.userRoleForm.userId,
          roleName: this.userRoleForm.roleName
        })
        this.$message.success('Role berhasil dihapus dari user')
        this.userRoleForm = { userId: '', roleName: '' }
      } catch (error) {
        this.$message.error('Gagal remove role: ' + error.message)
      }
    },

    async getUserRoles() {
      if (!this.userRoleForm.userId) {
        this.$message.warning('User ID harus diisi')
        return
      }
      
      try {
        const roles = await this.fetchUserRoles(this.userRoleForm.userId)
        if (roles && roles.length > 0) {
          this.$message.info(`User memiliki role: ${roles.join(', ')}`)
        } else {
          this.$message.info('User tidak memiliki role apapun')
        }
      } catch (error) {
        this.$message.error('Gagal mengambil role user: ' + error.message)
      }
    },

    handleSizeChange(val) {
      this.pageSize = val
      this.currentPage = 1
    },

    handleCurrentChange(val) {
      this.currentPage = val
    }
  }
}
</script>

