,<script>
export default {
  data() {
    return {
      dialogvisible: false,
      form: {password: null, konfirmasi_password: null, newPassword: null, confirmPassword: null},
      message: null
    }
  },
  computed: {
    get_user() {
      return this.$store.state.user.user
    },
    get_loading() {
      return this.$store.state.user.loading
    },
    // validation_newPassword() {
    //   const newPassword = this.form.newPassword;

    //   // Password validation regex patterns
    //   const hasUpperCase = /[A-Z]/.test(newPassword);
    //   const hasLowerCase = /[a-z]/.test(newPassword);
    //   const hasNumbers = /\d/.test(newPassword);
    //   const hasSpecialChars = /[!@#$%^&*(),.?":{}|<>]/.test(newPassword);
    //   const isValidLength = newPassword.length >= 8;

    //   // Check if the password meets all criteria
    //   if (!isValidLength || !hasUpperCase || !hasLowerCase || !hasNumbers || !hasSpecialChars) {
    //     this.message = 'Password must be at least 8 characters long and include uppercase letters, lowercase letters, numbers, and special characters.';
    //   }

    //   return this.message;
    // },
  },
  created() {
    this.$store.dispatch('user/get_user')
  },
  methods: {
    open_ganti_password: function() {
      this.dialogvisible = true;
      this.form.newPassword = null;
      this.form.confirmPassword = null;
      this.message = null;
    },
    cancel_ganti_password: function() {
      this.dialogvisible = false;
    },
    submit_ganti_password: function() {
      this.dialogvisible = false;
    },
    
    getAvatarSrc(gender) {
      if (gender === 'L') return require('@/assets/img/user_p.png');
      if (gender === 'P') return require('@/assets/img/user_w.png');
      return require('@/assets/img/user.png');
    },
    
    getGenderStatusClass(gender) {
      if (gender === 'L') return 'male-status';
      if (gender === 'P') return 'female-status';
      return 'unknown-status';
    },
    
    formatNIP(nip) {
      if (!nip) return '';
      return nip.replace(/(\d{8})(\d{6})(\d{1})(\d{3})/, '$1 $2 $3 $4');
    },
    validation_password() {
      const newPassword = this.form.newPassword == null ? null : this.form.newPassword;
      const confirmPassword = this.form.confirmPassword == null ? null : this.form.confirmPassword;
      const message = null;

      // Password validation regex patterns
      const hasUpperCase = /[A-Z]/.test(newPassword);
      const hasLowerCase = /[a-z]/.test(newPassword);
      const hasNumbers = /\d/.test(newPassword);
      const hasSpecialChars = /[!@#$%^&*(),.?":{}|<>]/.test(newPassword);
      const isValidLength = newPassword.length >= 8;

      // Check if the password meets all criteria
      if (!isValidLength || !hasUpperCase || !hasLowerCase || !hasNumbers || !hasSpecialChars) {
        message= 'Password must be at least 8 characters long and include uppercase letters, lowercase letters, numbers, and special characters.';
        return;
      }

      // Check if passwords match
      if (newPassword === confirmPassword) {
        message = 'Password changed successfully!';
        // Here you can add code to handle the password change, e.g., send it to the server
      } else {
        message = 'Passwords do not match. Please try again.';
      }

      return message;
    }
  },
}
</script>

