// Helper function to check if user can access admin features
function canAccessAdminFeatures(store) {
  const activeRole = store.state.activeRole;
  return activeRole === 'ROLE_ADMIN' || activeRole === 'ROLE_CURATOR';
}

// Helper function to check if user has specific role
function hasRole(store, requiredRole) {
  const activeRole = store.state.activeRole;
  return activeRole === requiredRole;
}

// Helper function to check if user has any of the required roles
function hasAnyRole(store, requiredRoles) {
  if (!requiredRoles || !Array.isArray(requiredRoles)) {
    return false;
  }
  
  const activeRole = store.state.activeRole;
  return requiredRoles.includes(activeRole);
}

router.beforeEach(async (to, from, next) => {
  // Check jika halaman tidak ditemukan (akan di-handle oleh catch-all route)
  // Hanya redirect ke maintenance untuk route yang benar-benar tidak valid
  if (to.matched.length === 0 && !to.path.startsWith('/dashboard')) {
    console.log('Route tidak ditemukan:', to.path)
    // Izinkan navigasi normal, biarkan catch-all route yang handle
    next()
    return
  }
  
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (!Store.getters.isLoggedIn) {
      next({ path: '/' })
      return
    }
    
    // Ensure roles are loaded
    if (!Store.state.roles || (Array.isArray(Store.state.roles) && Store.state.roles.length === 0)) {
      try {
        await Store.dispatch('get_roles');
      } catch (error) {
        console.error('Failed to get user roles:', error);
        next({ path: '/' })
        return
      }
    }
    
    // Check for admin requirement
    if (to.matched.some(record => record.meta.requiresAdmin)) {
      if (!canAccessAdminFeatures(Store)) {
        // Redirect to dashboard with error message
        next({ path: '/dashboard' })
        return
      }
    }
    
    // Check for specific role requirements
    const requiredRoles = to.matched.reduce((roles, record) => {
      if (record.meta.requiredRoles) {
        return [...roles, ...record.meta.requiredRoles];
      }
      return roles;
    }, []);
    
    if (requiredRoles.length > 0 && !hasAnyRole(Store, requiredRoles)) {
      next({ path: '/dashboard' })
      return
    }
    
    next()
  } else if (to.matched.some(record => record.meta.requiresVisitor)) {
    if (Store.getters.isLoggedIn) {
      next({ path: '/dashboard' })
      return
    }
    next()
  } else {
    next()
  }
})

export default router
