import Axios from 'axios'

const state = {
  pengumuman: [],
  pengumuman_detail: {},
  loading: false,
  error: null,
  host: 'https://api-intranet.lan.go.id', // Ganti dengan host API yang sesuai
  // host: 'http://localhost:8020', // Ganti dengan host API yang sesuai
  pagination: {
    currentPage: 0,
    totalPages: 0,
    totalElements: 0,
    pageSize: 10,
    hasNext: false,
    hasPrevious: false
  }
}

const getters = {
  getAllPengumuman: state => state.pengumuman,
  getPengumumanDetail: state => state.pengumuman_detail,
  isLoading: state => state.loading,
  getError: state => state.error,
  getPagination: state => state.pagination
}

const mutations = {
  SET_LOADING(state, status) {
    state.loading = status
  },
  SET_PENGUMUMAN(state, pengumuman) {
    state.pengumuman = pengumuman
  },
  SET_PENGUMUMAN_DETAIL(state, pengumuman) {
    state.pengumuman_detail = pengumuman
  },
  ADD_PENGUMUMAN(state, pengumuman) {
    state.pengumuman.unshift(pengumuman)
  },
  UPDATE_PENGUMUMAN(state, updatedPengumuman) {
    const index = state.pengumuman.findIndex(p => p.id === updatedPengumuman.id)
    if (index !== -1) {
      state.pengumuman.splice(index, 1, updatedPengumuman)
    }
  },
  DELETE_PENGUMUMAN(state, id) {
    state.pengumuman = state.pengumuman.filter(p => p.id !== id)
  },
  SET_ERROR(state, error) {
    state.error = error
  },
  SET_PAGINATION(state, paginationData) {
    state.pagination = {
      currentPage: paginationData.number,
      totalPages: paginationData.totalPages,
      totalElements: paginationData.totalElements,
      pageSize: paginationData.size,
      hasNext: !paginationData.last,
      hasPrevious: !paginationData.first
    }
  },
  CLEAR_ERROR(state) {
    state.error = null
  }
}

const actions = {
  async getPengumuman({ commit, rootState }, { page = 0, size = 10, search = '', kategori = '', aktif = '' } = {}) {
    try {
      commit('SET_LOADING', true)
      commit('CLEAR_ERROR')
      
      // Build query parameters
      const params = {
        page,
        size
      }
      
      // Add search parameter if provided
      if (search) {
        params.search = search
      }
      
      // Add kategori filter if provided
      if (kategori) {
        params.kategori = kategori
      }
      
      // Add aktif filter if provided
      if (aktif !== '') {
        params.aktif = aktif
      }
      
      // Get informasi data with pagination and filters
      const response = await Axios.get(`${state.host}/api/informasi`, {
        params,
        headers: {
          'Authorization': `Bearer ${rootState.token}`
        }
      })
      
      // Transform the data to match the frontend expected format
      const transformedData = response.data.content.map(item => ({
        id: item.id,
        judul: item.judul,
        isi: item.isiInformasi,
        kategori: item.kategori, // Keep original API kategori 
        tanggal_terbit: formatDateString(item.tglAwalTayang),
        tanggal_selesai: formatDateString(item.tglAkhirTayang),
        is_popup: item.kategori === 'PAMFLET', // Set popup true untuk pamflet
        link: item.link, // Now available from API
        gambar: item.imageUrl,
        aktif: item.aktif,
        posisi: item.posisi || 'TENGAH',
        publishedDate: item.publishedDate,
        createdAt: item.createdAt,
        updatedAt: item.updatedAt,
        createdBy: item.createdBy,
        updatedBy: item.updatedBy
      }))
      
      commit('SET_PENGUMUMAN', transformedData)
      commit('SET_PAGINATION', response.data)
    } catch (error) {
      commit('SET_ERROR', error.response?.data?.message || 'Gagal memuat pengumuman')
    } finally {
      commit('SET_LOADING', false)
    }
  },

  async getPengumumanById({ commit, rootState }, id) {
    try {
      commit('SET_LOADING', true)
      commit('CLEAR_ERROR')
      
      const response = await Axios.get(`${state.host}/api/informasi/${id}`, {
        headers: {
          'Authorization': `Bearer ${rootState.token}`
        }
      })
      
      const transformedData = {
        id: response.data.id,
        judul: response.data.judul,
        isi: response.data.isiInformasi,
        kategori: response.data.kategori,
        tanggal_terbit: formatDateString(response.data.tglAwalTayang),
        tanggal_selesai: formatDateString(response.data.tglAkhirTayang),
        is_popup: response.data.kategori === 'PAMFLET',
        link: response.data.link,
        gambar: response.data.imageUrl,
        aktif: response.data.aktif,
        posisi: response.data.posisi || 'TENGAH',
        publishedDate: response.data.publishedDate,
        createdAt: response.data.createdAt,
        updatedAt: response.data.updatedAt,
        createdBy: response.data.createdBy,
        updatedBy: response.data.updatedBy
      }
      
      commit('SET_PENGUMUMAN_DETAIL', transformedData)
      return transformedData
    } catch (error) {
      commit('SET_ERROR', error.response?.data?.message || 'Gagal memuat detail pengumuman')
      throw error
    } finally {
      commit('SET_LOADING', false)
    }
  },

  async createPengumuman({ commit, rootState }, pengumumanData) {
    try {
      commit('SET_LOADING', true)
      commit('CLEAR_ERROR')
      
      // Handle image upload or URL
      let imageUrl = null
      
      if (pengumumanData.gambar) {
        // Check if it's a file (for upload) or URL string
        if (pengumumanData.gambar instanceof File) {
          // Upload image file
          const formData = new FormData()
          formData.append('file', pengumumanData.gambar)
          
          const uploadResponse = await Axios.post(`${state.host}/api/informasi/upload`, formData, {
            headers: {
              'Authorization': `Bearer ${rootState.token}`,
              'Content-Type': 'multipart/form-data'
            }
          })
          imageUrl = uploadResponse.data.fileUrl
        } else if (typeof pengumumanData.gambar === 'string') {
          // Use URL directly
          imageUrl = pengumumanData.gambar
        }
      }
      
      // Create informasi object
      const informasiRequest = {
        judul: pengumumanData.judul,
        isiInformasi: pengumumanData.isi,
        kategori: pengumumanData.kategori, // Use API kategori directly
        tglAwalTayang: pengumumanData.tanggal_terbit,
        tglAkhirTayang: pengumumanData.tanggal_selesai,
        posisi: pengumumanData.posisi || 'TENGAH',
        aktif: pengumumanData.aktif !== false,
        imageUrl: imageUrl,
        link: pengumumanData.link || null
      }
      
      const response = await Axios.post(`${state.host}/api/informasi`, informasiRequest, {
        headers: {
          'Authorization': `Bearer ${rootState.token}`,
          'Content-Type': 'application/json'
        }
      })
      
      // Transform response to match frontend format
      const transformedData = {
        id: response.data.id,
        judul: response.data.judul,
        isi: response.data.isiInformasi,
        kategori: response.data.kategori,
        tanggal_terbit: formatDateString(response.data.tglAwalTayang),
        tanggal_selesai: formatDateString(response.data.tglAkhirTayang),
        is_popup: response.data.kategori === 'PAMFLET',
        link: response.data.link,
        gambar: response.data.imageUrl,
        aktif: response.data.aktif,
        posisi: response.data.posisi || 'TENGAH',
        publishedDate: response.data.publishedDate,
        createdAt: response.data.createdAt,
        updatedAt: response.data.updatedAt,
        createdBy: response.data.createdBy,
        updatedBy: response.data.updatedBy
      }
      
      commit('ADD_PENGUMUMAN', transformedData)
      return { data: transformedData }
    } catch (error) {
      commit('SET_ERROR', error.response?.data?.message || 'Gagal membuat pengumuman')
      throw error
    } finally {
      commit('SET_LOADING', false)
    }
  },

  async updatePengumuman({ commit, rootState }, { id, pengumumanData }) {
    try {
      commit('SET_LOADING', true)
      commit('CLEAR_ERROR')
      
      // Handle image upload or URL
      let imageUrl = pengumumanData.existing_image_url // Keep existing image URL by default
      
      if (pengumumanData.gambar) {
        // Check if it's a file (for upload) or URL string
        if (pengumumanData.gambar instanceof File) {
          // Upload new image file
          const formData = new FormData()
          formData.append('file', pengumumanData.gambar)
          
          const uploadResponse = await Axios.post(`${state.host}/api/informasi/upload`, formData, {
            headers: {
              'Authorization': `Bearer ${rootState.token}`,
              'Content-Type': 'multipart/form-data'
            }
          })
          imageUrl = uploadResponse.data.fileUrl
        } else if (typeof pengumumanData.gambar === 'string') {
          // Use URL directly
          imageUrl = pengumumanData.gambar
        }
      }
      
      // Create informasi object
      const informasiRequest = {
        judul: pengumumanData.judul,
        isiInformasi: pengumumanData.isi,
        kategori: pengumumanData.kategori, // Use API kategori directly
        tglAwalTayang: pengumumanData.tanggal_terbit,
        tglAkhirTayang: pengumumanData.tanggal_selesai,
        posisi: pengumumanData.posisi || 'TENGAH',
        aktif: pengumumanData.aktif !== false,
        imageUrl: imageUrl,
        link: pengumumanData.link || null
      }
      
      const response = await Axios.put(`${state.host}/api/informasi/${id}`, informasiRequest, {
        headers: {
          'Authorization': `Bearer ${rootState.token}`,
          'Content-Type': 'application/json'
        }
      })
      
      // Transform response to match frontend format
      const transformedData = {
        id: response.data.id,
        judul: response.data.judul,
        isi: response.data.isiInformasi,
        kategori: response.data.kategori,
        tanggal_terbit: formatDateString(response.data.tglAwalTayang),
        tanggal_selesai: formatDateString(response.data.tglAkhirTayang),
        is_popup: response.data.kategori === 'PAMFLET',
        link: response.data.link,
        gambar: response.data.imageUrl,
        aktif: response.data.aktif,
        posisi: response.data.posisi || 'TENGAH',
        publishedDate: response.data.publishedDate,
        createdAt: response.data.createdAt,
        updatedAt: response.data.updatedAt
      }
      
      commit('UPDATE_PENGUMUMAN', transformedData)
      return { data: transformedData }
    } catch (error) {
      commit('SET_ERROR', error.response?.data?.message || 'Gagal mengupdate pengumuman')
      throw error
    } finally {
      commit('SET_LOADING', false)
    }
  },

  async deletePengumuman({ commit, rootState }, id) {
    try {
      commit('SET_LOADING', true)
      commit('CLEAR_ERROR')
      
      await Axios.delete(`${state.host}/api/informasi/${id}`, {
        headers: {
          'Authorization': `Bearer ${rootState.token}`
        }
      })
      
      commit('DELETE_PENGUMUMAN', id)
    } catch (error) {
      commit('SET_ERROR', error.response?.data?.message || 'Gagal menghapus pengumuman')
      throw error
    } finally {
      commit('SET_LOADING', false)
    }
  },

  async toggleStatus({ commit, rootState }, id) {
    try {
      commit('SET_LOADING', true)
      commit('CLEAR_ERROR')
      
      const response = await Axios.put(`${state.host}/api/informasi/${id}/toggle-active`, {}, {
        headers: {
          'Authorization': `Bearer ${rootState.token}`
        }
      })
      
      // Transform response to match frontend format
      const transformedData = {
        id: response.data.id,
        judul: response.data.judul,
        isi: response.data.isiInformasi,
        kategori: response.data.kategori,
        tanggal_terbit: formatDateString(response.data.tglAwalTayang),
        tanggal_selesai: formatDateString(response.data.tglAkhirTayang),
        is_popup: response.data.kategori === 'PAMFLET',
        link: response.data.link,
        gambar: response.data.imageUrl,
        aktif: response.data.aktif,
        posisi: response.data.posisi || 'TENGAH',
        publishedDate: response.data.publishedDate,
        createdAt: response.data.createdAt,
        updatedAt: response.data.updatedAt,
        createdBy: response.data.createdBy,
        updatedBy: response.data.updatedBy
      }
      
      commit('UPDATE_PENGUMUMAN', transformedData)
      return { data: transformedData }
    } catch (error) {
      commit('SET_ERROR', error.response?.data?.message || 'Gagal mengubah status pengumuman')
      throw error
    } finally {
      commit('SET_LOADING', false)
    }
  },

  async syncInformasi({ commit, rootState }, options = {}) {
    try {
      commit('SET_LOADING', true)
      commit('CLEAR_ERROR')
      
      const syncType = options.type || 'latest'
      let endpoint
      
      if (syncType === 'all') {
        // Admin full sync
        endpoint = `${state.host}/api/informasi/sync/lan-api`
      } else {
        // Default - latest news sync  
        endpoint = `${state.host}/api/informasi/sync/latest-news`
      }
      
      const response = await Axios.post(endpoint, {}, {
        headers: {
          'Authorization': `Bearer ${rootState.token}`,
          'Content-Type': 'application/json'
        }
      })
      
      return response.data
    } catch (error) {
      const errorMessage = error.response?.data?.error || error.response?.data?.message || 'Gagal melakukan sinkronisasi data'
      commit('SET_ERROR', errorMessage)
      throw error
    } finally {
      commit('SET_LOADING', false)
    }
  }
}

// Helper functions
function formatDateString(dateString) {
  if (!dateString) return null
  // Handle both date formats: YYYY-MM-DD and ISO datetime
  if (dateString.includes('T')) {
    return dateString.split('T')[0] // Extract date part from ISO datetime
  }
  return dateString // Already in YYYY-MM-DD format
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
