            <el-button class="quicksand-300 uppercase" type="success" size="mini" icon="el-icon-place" v-if="get_absen.absenPulang == null && get_absen.absenPulangSitkp == null && koordinat_terkini.latitude != null && koordinat_terkini.longitude != null && get_absen.kondisi != null && get_absen.kegiatan != null && get_absen.output != null && get_absen.progres != null" :loading="loading" @click="submit_absen_pulang">Absen Pulang</el-button>
