            <el-button class="quicksand-300 uppercase" size="mini" icon="el-icon-edit" @click="open_dialogvisible(get_absen.kondisi, get_absen.kegiatan, get_absen.output, get_absen.progres)">Update Kegiatan, Output, dan Progress</el-button>
