            <p class="titillium-web-regular p-0 m-0">{{ get_absen.nama }}</p>
          </div>
          <div class="w-44">
            <p class="text-gray-600 text-sm text-left roboto-light p-0 m-0">{{ get_absen.absenMasukSitkp | konversi_waktu }} - {{ get_absen.absenPulangSitkp | konversi_waktu }}</p> 
          </div>
          <div class="w-44 ml-4">
            <p class="text-gray-600 text-sm text-right roboto-light p-0 m-0">Progress {{ get_absen.progres == null ? 0 : get_absen.progres }}%</p> 
