            <el-col :span="21">
              <p class="text-gray-400 text-sm text-justify roboto-light p-0 m-0">{{ get_absen.output == null ? 'Belum ada output' : get_absen.output | format_output }}</p>
            </el-col>
          </el-row>
          <el-row class="mt-2">
