            <p class="p-0 m-0 font-2">Output</p>
            <el-input type="textarea" :rows="4" placeholder="Please input" class="" v-model="form.output"></el-input>
          </div>
          <div class="mt-3">
            <p class="p-0 m-0 font-2">Progress (%)</p>
            <el-input-number class="" v-model="form.progress" size="small" controls-position="right" :min="0" :max="100"></el-input-number>
          </div>
          <div class="mt-3">
            <p class="p-0 m-0 font-2">Evidence</p>
            <el-input placeholder="Please input" class="" v-model="form.evidence"></el-input>
            <p class="p-0 m-0 text-xs text-yellow-500 font-1">*evidence berupa link google drive atau link lainnya</p>
          </div>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button size="mini" @click="close_dialogvisible">Cancel</el-button>
