</template>
<script>
import moment from 'moment'

import { latLng, Icon } from "leaflet";
import { LMap, LTileLayer, LMarker, LPopup } from "vue2-leaflet";
import "leaflet/dist/leaflet.css";

delete Icon.Default.prototype._getIconUrl;
Icon.Default.mergeOptions({
  iconRetinaUrl: require("leaflet/dist/images/marker-icon-2x.png"),
  iconUrl: require("leaflet/dist/images/marker-icon.png"),
  shadowUrl: require("leaflet/dist/images/marker-shadow.png"),
});
export default {
  props: {
    'deviceType': String
  },
  components: {
    LMap,
    LTileLayer,
    LMarker,
    LPopup
  },
  data() {
    return {
      zoom: 13,
      center: latLng(47.41322, -1.219482),
      url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      attribution:'&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',
      withPopup: latLng(47.41322, -1.219482),
      withTooltip: latLng(47.41422, -1.250482),
      currentZoom: 11.5,
      currentCenter: latLng(47.41322, -1.219482),
      showParagraph: false,
      mapOptions: { zoomSnap: 0.5 },
      showMap: true,
      koordinat_terkini: { latitude: null, longitude: null },
      loading: false,
      dialogvisible: false,
      form: { kondisi: "Sehat", kegiatan: null, output: null, output_1: [], output_2: null, progress: 0, absen_masuk: { lat: null, lon: null, lokasi: null }, absen_siang: { lat: null, lon: null, lokasi: null }, absen_pulang: { lat: null, lon: null, lokasi: null }, evidence: null },
    }
  },
  filters: {
    konversi_tanggal: function(value){
      moment.locale('id'); 
      return moment(value).format('dddd')+', '+moment(value).format('LL');
    },
    konversi_waktu: function(value){
      moment.locale('id'); 
      return value === null ? 'Belum Absen' : moment(value).format('HH.mm');
    },
    format_output: function(value) {
      if (value != null) {
        let abc = value.split("@");
        return abc[0];
      }
      return "Belum ada";
    },
    format_evidence: function(value) {
      if (value != null) {
        let abc = value.split("@");
        return abc[1];
      }
      return "Belum ada";
    },
  },
  computed: {
    get_absen() {
      let data_absen =  this.$store.state.absen.absen;
      return Object.keys(data_absen).length > 0 ? data_absen : 0
    },
    get_absen_loading() {
      return this.$store.state.absen.loading
    }
  },
  created() {
    this.$store.dispatch('absen/get_hari_ini')
    this.get_lokasi_terkini();
  },
  methods: {
    getViews: function(value) {
      if (this.$route.path !== value) {
        this.$router.push(value);
      }
    },
    get_lokasi_terkini: function() {
      this.loading = true;
      this.$watchLocation().then((coordinates) => {
        this.loading = false;
        this.koordinat_terkini.latitude = coordinates.lat;
        this.koordinat_terkini.longitude = coordinates.lng;
        // console.log(this.koordinat_terkini);
      });
    },
    zoomUpdate(zoom) {
      this.currentZoom = zoom;
    },
    centerUpdate(center) {
      this.currentCenter = center;
    },
    open_dialogvisible: function(kondisi, kegiatan, output, progress) {
      this.dialogvisible = true;

      let abc = output.split("@");

      this.form.kondisi = kondisi;
      this.form.kegiatan = kegiatan;
      this.form.output = abc[0];
      this.form.progress = progress;
      this.form.evidence = abc[1];
    },
    close_dialogvisible: function() {
      this.dialogvisible = false;  
    },
    submit_dialogvisible: function() {
      this.loading = true;

      let kondisi = this.form.kondisi;
      let kegiatan = this.form.kegiatan;
      let progress = this.form.progress;

      this.form.output_2 = "" + this.form.output + "@" + this.form.evidence;

      let output = this.form.output_2;

      this.$store.dispatch("absen/update_kegiatan_output_progress", {kondisi, kegiatan, output, progress})
        .then(() => {
          this.loading = false;
          this.form.kondisi = "Sehat";
          this.form.kegiatan = null;
          this.form.output = null;
          this.form.output_1 = [];
          this.form.output_2 = null;
          this.form.progress = 0;
          this.form.evidence = null;
          this.$store.dispatch("absen/get_hari_ini");
          this.dialogvisible = false;
        })
        .catch((error) => {
          console.log(error);
          this.loading = false;
        });
    },
    submit_absen_masuk: function() {
      this.loading = true;

      let longitude = this.koordinat_terkini.longitude;
      let latitude = this.koordinat_terkini.latitude;

      this.$store.dispatch("absen/post_absen_masuk", { longitude, latitude })
      .then(() => {
        this.loading = false;
        this.$store.dispatch("absen/get_hari_ini");
      })
      .catch((error) => {
        console.log(error);
        this.loading = false;
      });
    },
    submit_absen_siang: function() {
      this.loading = true;

      let longitude = this.koordinat_terkini.longitude;
      let latitude = this.koordinat_terkini.latitude;

      this.$store.dispatch("absen/post_absen_siang", { longitude, latitude })
      .then(() => {
        this.loading = false;
        this.$store.dispatch("absen/get_hari_ini");
      })
      .catch((error) => {
        console.log(error);
        this.loading = false;
      });
    },
    submit_absen_pulang: function() {
      this.loading = true;

      let longitude = this.koordinat_terkini.longitude;
      let latitude = this.koordinat_terkini.latitude;

      this.$store.dispatch("absen/post_absen_pulang", { longitude, latitude })
      .then(() => {
        this.loading = false;
        this.$store.dispatch("absen/get_hari_ini");
      })
      .catch((error) => {
        console.log(error);
        this.loading = false;
      });
    },
    refresh_web_page: function() {
      location.reload()
    }
  }
}
</script>